.Topbar{background:rgba(2,46,50,0);transition:2s ease;position:fixed;top:0;width:100vw;box-sizing:border-box;z-index:4;display:flex;align-items:baseline;height:63px;padding:0 20px;align-items:center}.Topbar>a{text-decoration:none;color:#fff;opacity:.8;font-size:1.2em;transform:translateY(-4px)}.Topbar>a:hover{opacity:1}.Topbar>a:before{content:"/";margin:0 10px}.Topbar>span{display:flex;align-items:center}.Topbar>span span:nth-of-type(2) img{height:26px;margin-left:13px;line-height:0}
