.Content{position:fixed;left:0;height:0;top:0;display:flex;color:#fff;transition:2.5s cubic-bezier(0.61, 0.01, 0.3, 1);z-index:3;opacity:1}.Content.noShow{transition:.2s ease;z-index:-1;opacity:0}.Content section:not(.IntroText){width:100vw;padding:10vh 50px;position:relative;height:100vh;display:flex;box-sizing:border-box}.Content section:not(.IntroText).noContent{height:0;padding:0}.Content section:not(.IntroText).right{justify-content:flex-end}.Content section:not(.IntroText).center{justify-content:center}.Content section:not(.IntroText)>article{height:100%;box-sizing:border-box;overflow:hidden;width:600px;display:flex;flex-direction:column;justify-content:flex-end}.Content section:not(.IntroText)>article.head{justify-content:flex-start}.Content section:not(.IntroText)>article p{font-size:.9em;padding:0}.Content section:not(.IntroText)>article h2{background:#022e32;padding:30px;margin:0;transition:2.5s cubic-bezier(0.61, 0.01, 0.3, 1);font-size:4em;line-height:1.2em;font-family:'SegoeBold','Arial','Sans-serif'}.Content section:not(.IntroText)>article>div{transition:2.5s cubic-bezier(0.61, 0.01, 0.3, 1);background:#022e32;padding:2vw}.Content .email{display:flex;flex-direction:column;font-size:2.5em;margin-top:80px;transition:2.5s cubic-bezier(0.61, 0.01, 0.3, 1)}.Content .email strong{font-family:'SegoeBold','Arial','Sans-serif';font-size:2.5em;line-height:1em}.Content .email span{line-height:1em}.Content .source{position:absolute;bottom:20px;right:80px;color:#fff}.Content .calendar{background:url("../../assets/icons/calendar.svg") no-repeat 25px center #022e32;background-size:auto 45px;position:absolute;bottom:20px;left:0;color:#fff;display:inline-flex;padding:20px 40px 20px 100px;font-size:2em;transition:2.5s cubic-bezier(0.61, 0.01, 0.3, 1)}
