.FooterAlt{background:#f2f2f2;margin-top:40px}.FooterAlt h3{color:#616161;font-size:15px;line-height:20px;margin:0 0 10px 0}.FooterAlt a,.FooterAlt span{font-size:12px;text-decoration:none;color:#616161;display:block;padding:8px 0}.FooterAlt .socials{background-color:#e3e3e3;display:flex;padding:20px 5vw;justify-content:space-between}.FooterAlt .socials h3{margin:0 20px 0 0}.FooterAlt .socials .follow{flex:3;display:flex;align-items:center}.FooterAlt .socials .share{flex:2;display:flex;align-items:center}.FooterAlt .socials .share ul{display:flex;align-items:center;list-style:none;margin:0;padding:0}.FooterAlt .socials .share ul li{margin-left:10px}.FooterAlt .socials .share ul li:first-child{margin:0}.FooterAlt .footerLinks{padding:40px 5vw 20px;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr;grid-gap:20px}.FooterAlt .footerLinks article ul{margin:0;padding:0;list-style:none}.FooterAlt .bottomLinks{padding:20px 5vw;display:flex;align-items:center}.FooterAlt .bottomLinks a,.FooterAlt .bottomLinks span{display:flex;align-items:center;padding:0 5px;margin:0}.FooterAlt .bottomLinks article{flex:3}.FooterAlt .bottomLinks ul{display:flex;margin:0;padding:0;list-style:none}@media only screen and (max-width: 1200px){.FooterAlt .footerLinks{grid-template-columns:1fr 1fr 1fr}.FooterAlt .bottomLinks{flex-direction:column;align-items:flex-start}.FooterAlt .bottomLinks ul{flex-direction:column}.FooterAlt .bottomLinks ul a{padding:8px 0}}@media only screen and (max-width: 600px){.FooterAlt .footerLinks{grid-template-columns:1fr}}
