@keyframes slide{from{transform:translate3d(100vw, 0, 0)}to{transform:translate3d(0, 0, 0)}}@keyframes zoom{from{transform:scale(0);opacity:0}to{opacity:1;transform:scale(1)}}.Anims.noShow{position:absolute;z-index:-1;opacity:0}.Anims>div{width:100vw;height:100vh;position:fixed;top:0;left:0;z-index:0}.Anims>div.triangles{transition:2s cubic-bezier(0.72, 0.01, 0.25, 1)}.Anims>div.triangles.intro{transform:scale(1);opacity:.5}.Anims>div.triangles.intro.big{transform:scale(2)}.Anims>div.triangles.radar{transition:1s cubic-bezier(0.73, -0.01, 0.25, 1);transform:scale(0.25) translate3d(0, 0, 0)}.Anims>div.triangles.radar.bigRadar{transform:scale(1.3) translate3d(15vw, 0, 0)}.Anims .showTriangle{transition:.5s ease;opacity:0;transform:scale(0);width:100vw;height:100vh;position:fixed;top:0;left:0}.Anims .showTriangle.show{opacity:1;transform:scale(1)}.Anims .padlock{transition:6s ease}.Anims .shadow.show{animation:slide ease 2s forwards}.Anims .darkweb{opacity:1;transition:.5s ease}.Anims .darkweb.out{opacity:0}.Anims .OutContent{position:fixed;top:0;width:100vw;height:100vh;box-sizing:border-box;color:#fff;z-index:5}.Anims .OutContent section{height:100vh}.Anims .OutContent section.Ethan1,.Anims .OutContent section.Ethan2,.Anims .OutContent section.Conclusion{opacity:0;animation:zoom ease 3s forwards}.Anims .OutContent section.Ethan1 blockquote,.Anims .OutContent section.Ethan2 blockquote,.Anims .OutContent section.Conclusion blockquote{font-size:.8em;width:240px}.Anims .OutContent section.Ethan1{animation-delay:3.2s}
