.Nav{position:fixed;z-index:5;top:0;right:0;display:flex}.Nav.open ul{transform:translateX(-20px);opacity:1;visibility:visible}.Nav.open>button{width:60px}.Nav.open>button span:after{opacity:0}.Nav.open>button:after{top:22px;transform:rotate(45deg)}.Nav.open>button:before{transform:rotate(-45deg);bottom:22px}.Nav>button{position:absolute;top:20px;right:20px;transition:0.7s cubic-bezier(0.9, 0.03, 0.25, 1);background:none;border:none;width:80px;height:50px;transform-origin:top right;transform:scale(0.4);z-index:2;outline:none;cursor:pointer}.Nav>button span:after,.Nav>button:after,.Nav>button:before{content:"";height:5px;width:100%;background:#fff;display:block;position:absolute;transform:rotate(0deg);transition:0.7s cubic-bezier(0.9, 0.03, 0.25, 1);left:0;border-radius:5px}.Nav>button span{font-size:0}.Nav>button span:after{opacity:1;top:22px}.Nav>button:after{top:0px}.Nav>button:before{bottom:0px}.Nav ul{opacity:1;background:#fff;border-radius:10px;list-style:none;padding:0;margin:0;box-shadow:0 10px 30px 0 #022d3190;transition:0.7s cubic-bezier(0.9, 0.03, 0.25, 1);transform:translateX(100%);position:relative;top:60px;visibility:visible}.Nav ul li{position:relative;border-top:1px solid #ececec}.Nav ul li:after{display:block;content:"";position:absolute;width:5px;height:5px;left:14px;top:18px;background:#00ead1;border-radius:40px;transition:1s cubic-bezier(0.8, 0.01, 0, 1.63);transform:scale(0)}.Nav ul li.selected:after{transform:scale(1.5)}.Nav ul li:first-child{border:none}.Nav ul a{display:block;text-decoration:none;padding:10px 20px 10px 30px;min-width:130px;color:#333}.Nav ul a img{opacity:.7}.Nav .btn{display:flex;justify-content:space-between}.Nav .btn a{flex:1;min-width:inherit;cursor:pointer}.Nav .btn a.disabled{opacity:.1;cursor:not-allowed}.Nav .btn a:nth-of-type(2){border-left:1px solid #ececec;text-align:right;padding:10px 30px 10px 20px}
