.ReadMore{border:1px solid #00ead1;padding:0 20px 20px;background:url("../../assets/images/radar.png") no-repeat 35px 15px;background-size:90px auto;margin:30px 0}.ReadMore .obj{margin-top:5px}.ReadMore .obj strong:after{content:":";margin:0 7px 0 3px}.ReadMore h4{font-family:'Segoe','Arial','Sans-serif';font-weight:100;font-size:1.5em;margin:20px 0}.ReadMore header{margin-left:130px;padding:20px;border-left:7px solid #00ead1}.ReadMore header h5,.ReadMore header h6{font-size:1.4em;margin:0;color:#00ead1}.ReadMore header h5{text-transform:uppercase}.ReadMore header h6{font-weight:100}.ReadMore.expanded .ctn{opacity:1}.ReadMore.expanded .ctn:after{height:0}.ReadMore .ctn{overflow:hidden;transition:0.7s cubic-bezier(0.99, -0.01, 0.25, 1);margin:20px 0;box-sizing:border-box;position:relative;opacity:.6}.ReadMore .ctn:after{content:"";position:absolute;transition:0.7s cubic-bezier(0.99, -0.01, 0.25, 1);width:100%;height:60px;background-image:linear-gradient(rgba(3,56,57,0), #033839);display:block;bottom:0;left:0}.ReadMore button{background:#00ead1;color:#022e32;border:none;padding:10px 20px;font-size:1.1em;font-family:'SegoeBold','Arial','Sans-serif';cursor:pointer}
