.Password{display:flex;flex-direction:column;margin:5vh}.Password h1{font-size:1em;font-weight:100}.Password ol{font-size:.8em;margin:0;padding:0;list-style:decimal inside;border:1px solid #eee;padding:20px;border-radius:5px;display:inline-block;width:500px;box-sizing:border-box}.Password h2{font-size:3em;margin:30px 0 0 0;font-weight:100}.Password h3{font-size:1em;text-transform:uppercase;color:#03a9f4;margin:5px 0 0 0;font-weight:100}.Password h4{font-size:.8em;margin:0 0 10px 0;opacity:.6;font-weight:100}.Password input{width:500px;box-sizing:border-box;border:1px solid #aaa;padding:5px 10px}.Password section article{display:flex}.Password section article div:not(:first-child){margin-left:100px}@media only screen and (max-width: 600px){.Password input,.Password{width:100%}.Password{width:100%;margin:0;padding:5vh;box-sizing:border-box}}
