.MHeader{background:#fff;padding-bottom:80px}.MHeader header{height:30vh;margin-bottom:50px;overflow:hidden;position:relative}.MHeader header video{width:100%;position:absolute;top:-50%;left:0}.MHeader h1{font-family:'Segoe','Arial','Sans-serif';font-weight:100;font-size:2.5em;line-height:1em;margin-bottom:40px}.MHeader h2{text-align:center;padding:15px 0;margin:0}.MHeader section{padding:0 20vw}.MHeader .scrollCtn{display:none}@media only screen and (max-width: 1024px){.MHeader section{padding:0 10vw}}@media only screen and (max-width: 814px){.MHeader header{height:inherit;overflow:visible}.MHeader header video{position:static}}
