.HeadIntro{height:100vh;text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center}.HeadIntro.show h1 span em{transform:translateY(0)}.HeadIntro.show a{display:block;transform:translateY(0);opacity:1}.HeadIntro a{transition:1.5s cubic-bezier(1, -0.01, 0.25, 1);opacity:0;transform:translateY(20px)}.HeadIntro h1{margin:0;color:#fff;font-size:4.2em;line-height:1.1em;font-family:'SegoeBold','Arial','Sans-serif'}.HeadIntro h1 span{display:block;overflow:hidden;line-height:1.4em;margin-bottom:-20px}.HeadIntro h1 span:nth-of-type(1) em{transition-duration:.8s}.HeadIntro h1 span:nth-of-type(2) em{transition-duration:1.1s}.HeadIntro h1 span:nth-of-type(3) em{transition-duration:1.4s}.HeadIntro h1 span:nth-of-type(4) em{transition-duration:1.7s}.HeadIntro h1 span em{display:block;font-style:normal;transform:translateY(1.4em);transition-timing-function:cubic-bezier(1, -0.01, 0.25, 1);transition-delay:.5s}.HeadIntro .LottieAnim{position:static;width:100px;height:inherit}
